@import url(https://fonts.googleapis.com/css2?family=Bree+Serif&family=Catamaran:wght@500&family=Original+Surfer&display=swap);

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Catamaran', sans-serif;
    background-color: rgb(247, 238, 238);
}
.App {
    width: 100%;
    height: 100vh;
}



.App .heading {
    padding: 2.5rem 0;
    text-align: center;
}
select {
    position: absolute;
    top: 22%;
    right: 20%;
    font-size: .90rem;

}
.App .flex {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 50%;
    margin: 0 auto;

}
.display {
    margin: 1rem 2rem 1rem 1rem;
    text-align: center;
    transition: all .2s ease-in-out; 
}
img {
    display: inline-block;
    max-width: 100%;
    margin-bottom: .1rem;
    width: 150px;
    border-radius: 50%;
    cursor: pointer;
    box-shadow: inset -1px 3px 8px 5px #6fa5e2, 2px 5px 16px 0px #4c5f74, 5px 5px 15px 5px rgba(0,0,0,0);
}

.display p {
    font-size: 1rem;
}
.display:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.user {
    width: 100%;
    height: 100vh;


}
.single-user{
    /* border: 1px solid black; */
    width: 30%;
    height: 50%;
    margin: 6rem auto;
    text-align: center;

}
.single-user img {
    border-radius: 0;
    width: 250px;
    margin: 1rem;
    box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0,0,0,0);
}

